<template>
  <div class="item-sales-wrapper container">
    <div v-if="loading" class="loading-overlay">
      <div class="overlay-content"><img src="@/assets/img/loading.gif" style="width: 60px" /></div>
    </div>
    <Header />
    <div class="content-wrapper">
      <div class="filter-toggle-wrapper" ref="toggleFilter">
        <span>
          <button class="filter-toggle" @click="toggleFilter()" :class="isFilterActive ? 'collasped' : ''"><fa :icon="['fas', 'sliders']"></fa></button>
        </span>
        <div class="filter-wrapper" :class="[isFilterActive ? 'slide-in' : 'slide-out']">
          <div class="filter-content-wrapper">
            <popup class="filter-popup-wrapper stores-popup">
              <template #header>
                <button class="filter-btn">
                  <!--<fa icon="store" />--> <span id="store-selection">{{ selectedCompanies.companyName }}</span><fa icon="angle-down" />
                </button>
              </template>
              <template #content>
                <div class="display-selection-wrapper">
                  <span v-for="company in companies" :key="company.id">
                    <input type="radio" name="store_type" :id="company.id" :value="company" v-model="selectedCompanies" @change="selectedStore" />
                    <fa class="checkmark" icon="check" />
                    <label :for="company.id">{{ company.companyName }}</label>
                  </span>
                </div>
              </template>
            </popup>
            <div class="filter-header">
              <div class="filter-popup-container">
                <popup class="filter-popup-wrapper">
                  <template #header>
                    <button class="filter-btn">
                      <span><!--<fa :icon="['fas', 'sliders']" />--><span id="display-selection">Daily</span></span><fa icon="angle-down" />
                    </button>
                  </template>
                  <template #content>
                    <div class="display-selection-wrapper">
                      <span>
                        <input type="radio" name="display_type" value="D" v-model="displayType" @change="selectedDisplayType" id="display-daily" />
                        <fa class="checkmark" icon="check" />
                        <label for="display-daily">Daily</label>
                      </span>
                      <span>
                        <input type="radio" name="display_type" value="W" v-model="displayType" @change="selectedDisplayType" id="display-weekly" />
                        <fa class="checkmark" icon="check" />
                        <label for="display-weekly">Weekly</label>
                      </span>
                      <span>
                        <input type="radio" name="display_type" value="M" v-model="displayType" @change="selectedDisplayType" id="display-monthly" />
                        <fa class="checkmark" icon="check" />
                        <label for="display-monthly">Monthly</label>
                      </span>
                    </div>
                  </template>
                </popup>
                <popup class="filter-popup-wrapper">
                  <template #header>
                    <button class="filter-btn">
                      <span><!--<fa :icon="['far', 'file-lines']" />--><span id="num-selection">Item #</span></span><fa icon="angle-down" />
                    </button>
                  </template>
                  <template #content>
                    <div class="num-selection-wrapper">
                      <span>
                        <input type="radio" name="show_item" value="Y" v-model="showItem" @change="selectedShowNum" id="item-num" checked="checked" />
                        <fa class="checkmark" icon="check" />
                        <label for="item-num">Item #</label>
                      </span>
                      <span>
                        <input type="radio" name="show_item" value="N" v-model="showItem" @change="selectedShowNum" id="upc" />
                        <fa class="checkmark" icon="check" />
                        <label for="upc">UPC</label>
                      </span>
                    </div>
                  </template>
                </popup>
              </div>
              <button class="search-btn btn btn-square" @click="searchWithFilter"><fa icon="magnifying-glass"></fa></button>
            </div>

            <DateType 
              @changeDateType="dateType = $event"
              @changeStartDate="startDate = $event"
              @changeEndDate="endDate = $event"
              :currentStartDate="startDate"
              :currentEndDate="endDate" />

            <Transition name="fade">
              <p v-if="invalidDateRange" class="invalid-search-msg">
                <fa icon="triangle-exclamation" />
                <span>Invalid date range</span>
              </p>
            </Transition>

            <div v-if="dateType == 'Single Date' || dateType == 'Today'" class="calendar-wrapper">
              <DatePicker @changeDateValue="startDate = $event" :defaultDate="startDate" labelEnableText="Date" />
            </div>
            <div v-if="dateType == 'Date Range' || dateType == 'This Week'" class="calendar-wrapper">
              <DatePicker @changeDateValue="startDate = $event" :defaultDate="startDate" :startDate="endDate" labelEnableText="From" />
              <DatePicker @changeDateValue="endDate = $event" :defaultDate="endDate" :startDate="startDate" :alwaysStartDate="true" labelEnableText="To" />
            </div>

            <label class="filter-label">Search By</label>
            <div>
              <div class="type-select">
                <popup class="filter-popup-wrapper">
                  <template #header>
                    <button class="filter-btn">
                      <span><!--<fa :icon="['fas', 'filter']" />--><span id="filter-selection">UPC</span></span><fa icon="angle-down" />
                    </button>
                  </template>
                  <template #content>
                    <div class="num-selection-wrapper">
                      <span>
                        <input type="radio" name="select_filter" value="Item #" v-model="selectedType" @change="selectedFilterType" id="item-num" checked="checked" />
                        <fa class="checkmark" icon="check" />
                        <label for="item-num">Item #</label>
                      </span>
                      <span>
                        <input type="radio" name="select_filter" value="UPC" v-model="selectedType" @change="selectedFilterType" id="upc" />
                        <fa class="checkmark" icon="check" />
                        <label for="upc">UPC</label>
                      </span>
                      <span>
                        <input type="radio" name="select_filter" value="Group of Items" v-model="selectedType" @change="selectedFilterType" id="dept-cat" />
                        <fa class="checkmark" icon="check" />
                        <label for="dept-cat">Group of Items</label>
                      </span>
                      <span>
                        <input type="radio" name="select_filter" value="Special" v-model="selectedType" @change="selectedFilterType" id="special" />
                        <fa class="checkmark" icon="check" />
                        <label for="special">Special</label>
                      </span>
                    </div>
                  </template>
                </popup>
                <p class="filter-desc" v-if="selectedType == 'Item #'">Enter multiple Item # delimited by ' , '</p>
              </div>

              <Transition name="fade">
                <p v-if="invalidSearch" class="invalid-search-msg">
                  <fa icon="triangle-exclamation" />
                  <span>Please enter {{ selectedType }}</span>
                </p>
              </Transition>
              
              <Transition name="fade">
                <div class="filter-option" v-if="selectedType == 'Item #'">
                  <textarea v-model="itemNum"></textarea>
                </div>
              </Transition>
              <Transition name="fade">
                <div class="filter-option text-input" v-if="selectedType == 'UPC'">
                  <input v-if="selectedType == 'UPC'" @keyup.enter="searchWithFilter" type="text" v-model="upc" />
                </div>
              </Transition>
              <Transition name="fade">
                <div class="filter-option" v-if="selectedType == 'Group of Items'">
                  <label>
                    <span>Type</span>
                    <select v-model="selectedAmtType">
                      <option v-for="amtType in amtTypes" :key="amtType.qty_type" :value="amtType.qty_type">{{ amtType.qty_dsc }}</option>
                    </select>
                  </label>
                  <label>
                    <span>Category</span>
                    <select v-model="selectedCategory">
                      <option value="">--</option>
                      <option v-for="category in categories" :key="category.cat_no" :value="category.cat_pathkey">{{ category.cat_display }}</option>
                    </select>
                  </label>
                  <label>
                    <span>Department</span>
                    <select v-model="selectedDept">
                      <option value="">--</option>
                      <option v-for="dept in depts" :key="dept.dep_no" :value="dept.dep_no">{{ dept.dep_display }}</option>
                    </select>
                  </label>
                </div>
              </Transition>
              <Transition name="fade">
                <div class="filter-option special-filter" v-if="selectedType == 'Special'">
                  <select v-model="selectedOption" class="special-type">
                    <option value="">--</option>
                    <option v-for="special in specials" :key="special.id" :value="special.id">{{ special.display }}</option>
                  </select>
                  <select v-if="selectedOption == '1'" v-model="specialOption">
                    <option v-for="origin in origins" :key="origin.id" :value="origin.id">{{ origin.display }}</option>
                  </select>
                  <select v-else-if="selectedOption == '2'" v-model="specialOption">
                    <option v-for="brand in brands" :key="brand.id" :value="brand.id">{{ brand.display }}</option>
                  </select>
                  <select v-else-if="selectedOption == '3'" v-model="specialOption">
                    <option v-for="employee in employees" :key="employee.id" :value="employee.id">{{ employee.display }}</option>
                  </select>
                  <select v-else-if="selectedOption == '4'" v-model="specialOption">
                    <option v-for="vendor in vendors" :key="vendor.id" :value="vendor.id">{{ vendor.display }}</option>
                  </select>
                </div>
              </Transition>
            </div>
          </div>
        </div>
      </div>

      <div class="content" v-bind:style="{ height: 'calc(' + windowHeight + 'px - 90px)' }">
        <div v-if="(!empty && itemSalesData.total_amount <= 0) || (!empty && !itemSalesData.total_amount)" class="no-results"><fa :icon="['fas', 'magnifying-glass']" />No search results found</div>
        <div v-if="!empty && itemSalesData.total_amount > 0" class="item-sales-list-wrapper">

        <div class="item-sale-list" :class="showItemList ? 'expand' : ''" v-bind:style="{ height: 'calc(' + windowHeight + 'px - 130px)' }">
          <h5>Item Sales List</h5>
          <li v-for="itemSale in itemSales" :key="itemSale" @click="scroll(itemSale.item_no)" :class="{highlight: highlightItem === itemSale.item_no}">
            <p v-if="showItemNum">{{ itemSale.item_no }}</p>
            <p v-else-if="!showItemNum">{{ itemSale.upc }}</p>
            <p>{{ itemSale.itemname }}</p>
            <p>{{ itemSale.itemdesc }} {{ itemSale.itemnetwt }}</p>
          </li>
        </div>
        <button class="items-list-btn" :class="showItemList ? 'expand' : ''" @click="showItemList = !showItemList"><fa icon="angle-right" /></button>
     
        <div class="item-list-wrapper">
          <div class="item-sale-list item-sale-list-mobile" :class="showItemList ? 'expand' : ''" v-bind:style="{ height: 'calc(' + windowHeight + 'px - 130px)' }">
            <h5>Item Sales List</h5>
            <li v-for="itemSale in itemSales" :key="itemSale" @click="scroll(itemSale.item_no)" :class="{ highlight: highlightItem == itemSale.item_no }">
              <p v-if="showItemNum">{{ itemSale.item_no }}</p>
              <p v-else-if="!showItemNum">{{ itemSale.upc }}</p>
              <p>{{ itemSale.itemname }}</p>
              <p>{{ itemSale.itemdesc }} {{ itemSale.itemnetwt }}</p>
            </li>
          </div>
          <button class="items-list-btn items-list-btn-mobile" :class="showItemList ? 'expand' : ''" @click="showItemList = !showItemList"><fa icon="angle-right" /></button>
        </div>
          
          <DataTable
              id="rpt-table"
              class="tb-wrapper"
              v-model:expandedRows="state.expandedRows"
              dataKey="period"
              :value="itemSales"
              rowGroupMode="subheader"
              groupRowsBy="upc"
              scrollable
              :scrollHeight="'calc(' + windowHeight + 'px - 220px)'"
              :exportFilename="fileName"
              ref="dt"
            >
            
              <template #header>
                <div class="table-header-wrapper">
                  <div class="table-header">
                    <h3>{{ $t('header.' + this.$route.name) }} - {{ storeName }}</h3>
                    <p v-if="singleDateActive">As of date {{ formatDate(rptStartDate) }}</p>
                    <p v-else-if="dateRangeActive">As of date {{ formatDate(rptStartDate) }} - {{ formatDate(rptEndDate) }}</p>
                  </div>
                  <div class="table-btn-group">
                    <button class="print-btn btn btn-circle" onclick="window.print()"><fa icon="print"></fa></button>
                    <button class="excel-btn btn btn-circle" @click="exportCSV($event)"><fa icon="file-csv"></fa></button>
                  </div>
                </div>
              </template>
              <template #groupheader="slotProps">
                <span :id="slotProps.data.item_no" class="item-wrapper"></span>
                <p class="item" :class="{highlight: highlightItem === slotProps.data.item_no}">
                  <span v-if="showItemNum">{{ slotProps.data.item_no }}</span>
                  <span v-else-if="!showItemNum">{{ slotProps.data.upc }}</span
                  ><span> - {{ slotProps.data.itemname }} {{ slotProps.data.itemdesc }} {{ slotProps.data.itemnetwt }}</span>
                </p>
              </template>
              <Column field="period" header="Date">
                <template #body="slotProps">
                  <ul v-for="(detail, index) in slotProps.data.details" :key="key('period', index, detail.period)">
                    <li>{{ detail.period }}</li>
                  </ul>
                  <p class="bold-600">Total</p>
                </template>
              </Column>
              <Column class="qty-col" field="qty" header="Quantity">
                <template #body="slotProps">
                  <ul v-for="(detail, index) in slotProps.data.details" :key="key('qty', index, detail.qty)">
                    <li>{{ detail.qty }}</li>
                  </ul>
                  <p class="bold-600">{{ slotProps.data.subtotal_qty }}</p>
                </template>
              </Column>
              <Column class="average" field="average" header="Average($)">
                <template #body="slotProps">
                  <ul v-for="(detail, index) in slotProps.data.details" :key="key('avg', index, detail.average)">
                    <li>{{ detail.average }}</li>
                  </ul>
                  <p class="bold-600">{{ slotProps.data.details[0].average }}</p>
                </template>
              </Column>
              <Column class="amount" field="amount" header="Amount($)">
                <template #body="slotProps">
                  <ul v-for="(detail, index) in slotProps.data.details" :key="key('amt', index, detail.amount)">
                    <li>{{ detail.amount }}</li>
                  </ul>
                  <p class="bold-600">{{ slotProps.data.subtotal_amount }}</p>
                </template>
              </Column>
              <Column field="" header="" style="display: none"></Column>
            </DataTable>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import { reactive } from "vue";
import axios from "axios";
import moment from "moment";
import Header from "@/components/Header.vue";
import Popup from "@/components/Popup.vue";
import DatePicker from "@/components/DatePicker.vue";
import DateType from "@/components/DateType.vue";

export default {
  components: {
    Header,
    Popup,
    DatePicker,
    DateType
  },
  setup() {
    const state = reactive({
      expandedRows: {},
    });

    const onExpandRow = (id) => {
      const checkProp = id in state.expandedRows;
      if (checkProp) {
        delete state.expandedRows[id];
      } else {
        state.expandedRows[id] = true;
      }
      state.expandedRows = { ...state.expandedRows };
    };

    return {
      state,
      onExpandRow,
    };
  },
  data() {
    return {
      windowHeight: window.innerHeight,
      isFilterActive: true,
      empty: true,
      companies: [
        {
        id: 1027,
        companyName: "Apple Supermarket 1"
        },
        {
        id: 1221,
        companyName: "Apple Supermarket 2"
        },
      ],
      invalidStore: false,
      selectedCompanies: [],
      loading: false,
      storeName: "",
      itemSalesData: [],
      itemSales: [],
      startDate: "",
      endDate: "",
      rptStartDate: "",
      rptEndDate: "",
      selectedType: "UPC",
      dateType: "Date Range",
      displayType: "D",
      offset: 2,
      showItem: "Y",
      itemNum: [],
      upc: "",
      deptCat: [],
      amtTypes: "",
      categories: "",
      depts: "",
      brands: "",
      employees: "",
      origins: "",
      vendors: "",
      specials: [
        { id: 1, display: "Origin" },
        { id: 2, display: "Brand" },
        { id: 3, display: "Handle By" },
        { id: 4, display: "Pr. Vendor" },
      ],
      selectedAmtType: "S",
      selectedCategory: "",
      selectedDept: "",
      selectedOption: 0,
      specialOption: "",
      filterData: "",
      showItemNum: true,
      itemSelected: [],
      itemAvg: "",
      invalidSearch: false,
      invalidDateRange: false,
      showItemList: false,
      highlightItem: "",
      singleDateActive: false,
      dateRangeActive: false
    };
  },
  mounted() {
    // this.companies = JSON.parse(localStorage.getItem("companies"));

    this.selectedCompanies = this.companies[0];

    this.$nextTick(() => {
      window.addEventListener("resize", this.onResize);
    });

    axios
      .get("https://cloudbackofficereportapidev.azurewebsites.net/AmtType", {
        headers: {
          marketid: this.selectedCompanies.id,
        },
      })
      .then((response) => {
        this.amtTypes = response.data.datas;
      })
      .catch((error) => console.log(error.response));

    axios
      .get("https://cloudbackofficereportapidev.azurewebsites.net/Category", {
        headers: {
          marketid: this.selectedCompanies.id,
        },
      })
      .then((response) => {
        this.categories = response.data.datas;
      })
      .catch((error) => console.log(error.response));

    axios
      .get("https://cloudbackofficereportapidev.azurewebsites.net/Department", {
        headers: {
          marketid: this.selectedCompanies.id,
        },
      })
      .then((response) => {
        this.depts = response.data.datas;
      })
      .catch((error) => console.log(error.response));

    axios
      .get("https://cloudbackofficereportapidev.azurewebsites.net/DropDown/Brand", {
        headers: {
          marketid: this.selectedCompanies.id,
        },
      })
      .then((response) => {
        this.brands = response.data.datas;
      })
      .catch((error) => console.log(error.response));

    axios
      .get("https://cloudbackofficereportapidev.azurewebsites.net/DropDown/Employees", {
        headers: {
          marketid: this.selectedCompanies.id,
        },
      })
      .then((response) => {
        this.employees = response.data.datas;
      })
      .catch((error) => console.log(error.response));

    axios
      .get("https://cloudbackofficereportapidev.azurewebsites.net/DropDown/Origin", {
        headers: {
          marketid: this.selectedCompanies.id,
        },
      })
      .then((response) => {
        this.origins = response.data.datas;
      })
      .catch((error) => console.log(error.response));

    axios
      .get("https://cloudbackofficereportapidev.azurewebsites.net/DropDown/VendorCode", {
        headers: {
          marketid: this.selectedCompanies.id,
        },
      })
      .then((response) => {
        this.vendors = response.data.datas;
      })
      .catch((error) => console.log(error.response));
  },
  // beforeMount() {
  //   window.addEventListener('click', this.close);
  // },
  // unmounted() {
  //   window.removeEventListener('click', this.close);
  // },
  beforeDestroy() {
    window.removeEventListener("resize", this.onResize);
  },
  methods: {
    scroll(item) {
      const el = document.getElementById(item);
      el.scrollIntoView({
        behavior: 'smooth',
        block: 'start'
       });

      this.showItemList = false;
      this.highlightItem = item;
      setTimeout(() => {
        this.highlightItem = "";
      }, 5000); 
        
    },
    key (field, index, item) {
      return `${field}-${index}-${item}`
    },
    toggleFilter() {
      this.isFilterActive = !this.isFilterActive;
    },
    // close(e) {
    //   if (!this.$refs.toggleFilter.contains(e.target)) {
    //     this.isFilterActive = false;
    //   }
    // },
    formatPrice(value) {
      if (!value || value == 0) {
        return "0.00";
      }
      let val = (value / 1).toFixed(2).replace(",", ".");
      return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
    formatNumber(value) {
      return value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
    formatDate(date) {
      return moment(date).format("MMM DD, yyyy");
    },
    exportCSV() {
      this.$refs.dt.exportCSV();
    },
    selectedDisplayType() {
      var value = "Nothing selected",
        selected = document.querySelector('input[name="display_type"]:checked'),
        selection = document.querySelector("#display-selection");
      if (selected) {
        value = document.querySelector('label[for="' + selected.id + '"]').innerHTML;
      }
      selection.innerHTML = value;
    },
    selectedShowNum() {
      var value = "Nothing selected",
        selected = document.querySelector('input[name="show_item"]:checked'),
        selection = document.querySelector("#num-selection");
      if (selected) {
        value = document.querySelector('label[for="' + selected.id + '"]').innerHTML;
      }
      selection.innerHTML = value;
    },
    selectedStore() {
      var value = "Nothing selected",
        selected = document.querySelector('input[name="store_type"]:checked'),
        selection = document.querySelector("#store-selection");
      if (selected) {
        value = document.querySelector('label[for="' + selected.id + '"]').innerHTML;
      }
      selection.innerHTML = value;
    },
    selectedFilterType() {
      var value = "Nothing selected",
        selected = document.querySelector('input[name="select_filter"]:checked'),
        selection = document.querySelector("#filter-selection");

      if (selected) {
        value = document.querySelector('label[for="' + selected.id + '"]').innerHTML;
      }

      selection.innerHTML = value;
    },
    calculateSalesAvg(values) {
      var total = 0,
        length = values.length;
      for (let value of values) {
        total += value.average;
      }
      return "$" + (total / length).toFixed(2);
    },
    searchWithFilter() {
      this.loading = true;
      this.isFilterActive = false;
      this.itemSelected = "";
      this.itemSales = [];
      this.storeName = this.selectedCompanies.companyName;

      this.rptStartDate = "";
      this.rptEndDate = ""

      if (this.dateType == 'Single Date' || this.dateType == 'Today'){
        this.rptStartDate = moment(this.startDate).format("YYYY-MM-DD");
        this.rptEndDate = moment(this.startDate).format("YYYY-MM-DD");
        this.singleDateActive = true;
        this.dateRangeActive = false;
      } else if (this.dateType == 'Date Range' || this.dateType == 'This Week'){
        this.dateRangeActive = true;
        this.singleDateActive = false;
        const days = Math.round((this.endDate - this.startDate) / (1000 * 60 * 60 * 24));

        if (this.startDate && this.endDate && days >= 0) {
          this.rptStartDate = moment(this.startDate).format("YYYY-MM-DD");
          this.rptEndDate = moment(this.endDate).format("YYYY-MM-DD");
        } else if (days < 0) {
          this.invalidDateRange = true;
          this.rptStartDate = "";
          this.rptEndDate = "";
        } else {
          this.rptStartDate = "";
          this.rptEndDate = "";
        }
      }

      if (this.showItem == "Y") {
        this.showItemNum = true;
      } else if (this.showItem == "N") {
        this.showItemNum = false;
      }

      if (this.selectedType == "Item #") {
        if (this.itemNum.indexOf(",") > -1) {
          this.itemNum = this.itemNum.replace(" ", "").split(",");
        }
        if (this.itemNum) {
          this.invalidSearch = false;
        } else {
          this.invalidSearch = true;
        }
        this.filterData = {
          itemnos: this.itemNum,
          sDate: this.rptStartDate,
          eDate: this.rptEndDate,
          type: this.displayType,
          offset: this.offset,
          show_upc: this.showItem,
        };
      } else if (this.selectedType == "UPC") {
        if (this.filterData) {
          this.invalidSearch = false;
        } else {
          this.invalidSearch = true;
        }
        this.filterData = {
          upc: this.upc,
          sDate: this.rptStartDate,
          eDate: this.rptEndDate,
          type: this.displayType,
          offset: this.offset,
          show_upc: this.showItem,
        };
      } else if (this.selectedType == "Group of Items") {
        if (this.selectedCategory || this.selectedDept) {
          this.invalidSearch = false;
        } else {
          this.invalidSearch = true;
        }
        this.filterData = {
          sDate: this.rptStartDate,
          eDate: this.rptEndDate,
          type: this.displayType,
          offset: this.offset,
          show_upc: this.showItem,
          deptCat: {
            amt_type: this.selectedAmtType,
            cat_pathkey: this.selectedCategory,
            dep_no: "" + this.selectedDept + "",
          },
        };
      } else if (this.selectedType == "Special") {
        if (this.selectedOption) {
          this.invalidSearch = false;
        } else {
          this.invalidSearch = true;
        }
        this.filterData = {
          sDate: this.rptStartDate,
          eDate: this.rptEndDate,
          type: this.displayType,
          offset: this.offset,
          show_upc: this.showItem,
          sp_type: this.selectedOption,
          sp_value: this.specialOption,
        };
      }

      if (this.invalidSearch) {
        this.loading = false;
        this.isFilterActive = true;
      } else if (this.invalidDateRang) {
        this.loading = false;
        this.isFilterActive = true;
      } else {
        this.invalidSearch = false;
        this.invalidDateRang = false;

        axios
          .post("https://cloudbackofficereportapidev.azurewebsites.net/Report/ItemSales", this.filterData, {
            headers: {
              marketid: this.selectedCompanies.id,
            },
          })
          .then((response) => {
            this.itemSalesData = response.data.datas;
            this.itemSales = response.data.datas.itemSalesRptItems;
          })
          .catch((error) => console.log(error.response))
          .finally(() => {
            this.loading = false;
            this.empty = false;
          });
      }
    },
    // getItem(value) {
    //   this.itemSelected = value;
    //   this.itemAvg = this.itemSelected.details[0].average;
    // },
    // backItemList() {
    //   this.itemSelected = "";
    // },
  },
  computed: {
    fileName() {
      let excelName = this.$route.name + " " + this.startDate + "-" + this.endDate;
      return excelName;
    },
    totalQty() {
      let total = 0;
      for (let itemSale of this.itemSales) {
        total += itemSale.subtotal_qty;
      }
      return total;
    },
    totalAmount() {
      let total = 0;
      for (let itemSale of this.itemSales) {
        total += itemSale.subtotal_amount;
      }
      return total;
    },
  },
};
</script>
